<template>
  <div id="app">

  <div id="notifications-bar">
    <notifications group="foo" position="top center" :max="1"/>
    <div class="score">Growth Score : {{ score }}</div>
  </div>

  <div id="under-notifications-bar" class="full">
    <div v-if="question" class="full" id="main-container">
      <div id="flashcard-slot" class="full">
        <flashcard :front="question" :back="answers"></flashcard>
      </div>
    </div>

    <div v-else>
      <h1 class="done-for-today-message">
        Done for today!
      </h1>
    </div>
  </div>

    <div id="answer-slot">
      <div v-if="!this.$store.state.cardFlipped">
        <button v-for="answer in answers" :key="answer.text" @click="submit(answer)">
          {{answer.text}} 
        </button>
      </div>
      <div v-else>
        <button @click="flipIfFlipped()">
          Next
        </button>
      </div>
    </div>
      
    

  </div>
</template>

<script>
import flashcard from "./components/Flashcard.vue";

export default {
  components: {
    flashcard
  },
  data() {
    return {
      score: 1
    };
  },
  computed: {
    question() {
      return this.$store.getters.currentQuestion; // handled by vuex
    },
    answers() {
      return this.$store.getters.currentAnswers; // handled by vuex
    }
  },
  methods: {
    correct() {
      this.$store.dispatch("correctAnswer"); // handled by vuex
      this.score++;
    },
    wrong() {
      this.$store.dispatch("wrongAnswer"); // handled by vuex
    },
    submit(answer) {
      if (this.answers.length === 1) {
        this.$store.commit("flipCard");
        this.$store.dispatch("correctAnswer");
        this.score++;
        this.$store.dispatch("init");
        return;
      }
      if (answer.correct) {
        this.$store.dispatch("correctAnswer"); // handled by vuex
        this.score++;
        this.$notify({
          group: "foo",
          title: "Good job!",
          duration: 600,
          speed: 200,
          type: "success"
        });
      } else {
        this.$store.dispatch("wrongAnswer"); // handled by vuex
        this.$notify({
          group: "foo",
          title: "You'll get there!",
          duration: 600,
          speed: 200,
          type: "error"
        });
      }
    },
    flipIfFlipped() {
      this.$store.commit("flipCard"); // handled by vuex
      this.$store.dispatch("init");
    }
  }
};
</script>

