<template>
  <div>
    <div v-bind:style="{backgroundColor: colorFront, color: colorTextFront}" v-show="!isToggle" class="animated flipInX flashcard">
      <div class="card-content center">
        <p v-bind:style="{fontSize: textSizeFront,fontWeight: 'bold'}" v-html="front"></p>
      </div>
      <div class="card-footer">{{footerFront}}</div>
    </div>
    <div v-bind:style="{backgroundColor: colorFront, color: colorTextFront}" v-show="isToggle" class="animated flipInX flashcard">
      <div class="card-content center">
        <p v-bind:style="{fontSize: textSizeBack, fontWeight: 'bold'}">
          <span v-for="answer in orderedAnswers" :key="answer.text"> 
            <p v-if="answer.correct" class="correct-answer"> {{answer.text}}</p> 
            
            <p v-else> 
              <span class="incorrect-answer"> {{answer.text}} </span>

              <span v-if="answer.incorrectMessage" class="incorrect-hint">
                 {{answer.incorrectMessage}}
              </span> 
            </p>

          </span>
        </p>
      </div>
      <div class="card-footer">{{footerBack}}</div>
    </div>
  </div>
</template>

<script>
import _ from "lodash";

// Inspired by https://github.com/cuduy197/vue-flashcard
export default {
  props: {
    front: {
      type: String,
      default: "default front"
    },
    back: {
      type: Array,
      default: function() {
        return [];
      }
    },
    textSizeFront: {
      type: String,
      default: "2em"
    },
    textSizeBack: {
      type: String,
      default: "2em"
    },
    colorTextFront: {
      type: String,
      default: "black"
    },
    colorTextBack: {
      type: String,
      default: "white"
    },
    colorFront: {
      type: String,
      default: "white"
    },
    colorBack: {
      type: String,
      default: "#2ecc71"
    },
    footerFront: {
      type: String,
      default: ""
    },
    footerBack: {
      type: String,
      default: ""
    }
  },
  computed: {
    isToggle() {
      return this.$store.state.cardFlipped; // handled by vuex
    },
    orderedAnswers: function() {
      return _.orderBy(this.back, "correct").reverse();
    }
  },
  methods: {
    flipIfFlipped() {
      if (this.isToggle) {
        this.$store.commit("flipCard"); // handled by vuex
        this.$store.dispatch("init");
      }
    }
  }
};
</script>

<style scoped>
.card-content {
  font-size: 0.6rem;
}
.center {
  text-align: center;
}
.flashcard {
  /* border-radius: 10px; */
  margin: 0 auto;
  padding: 0.5rem 1rem;
  /* box-shadow: 0 0px 10px rgba(0, 0, 0, 0.4); */
  text-align: center;
}
.animated {
  animation-duration: 0.75s;
  animation-fill-mode: both;
}
.correct-answer {
  font-weight: 800;
}
.incorrect-answer {
  font-weight: 400;
  text-decoration: line-through;
}
.incorrect-hint {
  margin-left: 1rem;
  font-weight: 400;
}
@keyframes flipInX {
  from {
    transform: perspective(400px) rotate3d(1, 0, 0, 90deg);
    animation-timing-function: ease-in;
    opacity: 0;
  }
  40% {
    transform: perspective(400px) rotate3d(1, 0, 0, -10deg);
    animation-timing-function: ease-in;
  }
  60% {
    transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
    opacity: 1;
  }
  80% {
    transform: perspective(400px) rotate3d(1, 0, 0, -5deg);
  }
  to {
    transform: perspective(400px);
  }
}
.flipInX {
  backface-visibility: visible !important;
  animation-name: flipInX;
}
</style>
